import React, { Fragment } from 'react';
import Link from 'next/link';

const Navigation = () => {
  return (
    <Fragment>
      <ul>
        <Link href="/">
          <a>Главная</a>
        </Link>
        <Link href="/accountant">
          <a>Бухгалтерские услуги</a>
        </Link>
        <Link href="/registration-ooo">
          <a>Регистрация ООО</a>
        </Link>
        <Link href="/registration-flp">
          <a>Регистрация ФЛП</a>
        </Link>
        <Link href="/about">
          <a>О нас</a>
        </Link>
      </ul>
      <style jsx>{`
        ul {
          background-color: #74d9f2;
          height: 50px;
        }

        a {
          font-family: 'Montserrat';
          font-size: 18px;
          color: #000;
          line-height: 50px;
          margin-left: 15px;
        }

        a:first-child {
          margin-left: 0;
        }

        `}</style>
    </Fragment>
  );
}

export default Navigation;
